/* tablets */

/* @media (max-width: 991px) {} */

/* only small tablets */

@media (min-width: 768px) and (max-width: 991px) {
    #hero-area .contents h1 {
        font-size: 28px;
    }
    .item-boxes h4 {
        font-size: 18px;
    }
    #features .show-box img {
        display: none;
    }
    .pricing-table .pricing-details ul li {
        font-size: 12px;
    }
    .counters .facts-item .fact-count h4 {
        font-size: 14px;
    }
    .single-team {
        margin-bottom: 30px;
    }
    .meta-tags span {
        margin-right: 3px;
    }
    .meta-tags span i {
        margin-right: 0;
    }
    #blog .blog-item-text h3 {
        font-size: 14px;
        line-height: 20px;
    }
}

/* mobile or only mobile */

@media (max-width: 767px) {
    .section-header .section-title {
        font-size: 20px;
    }
    #hero-area .contents {
        padding: 100px 0 80px;
    }
    #hero-area .contents h1 {
        font-size: 18px;
        line-height: 30px;
    }
    #features .show-box img {
        display: none;
    }
    #features .box-item .text h4 {
        font-size: 14px;
    }
    .controls {
        margin: 0;
        .btn {
            font-size: 13px;
            padding: 7px 7px;
        }
    }
    .video-promo .video-promo-content h2 {
        font-size: 22px;
    }
    .pricing-table {
        margin-bottom: 15px;
    }
    .counters .facts-item {
        margin-bottom: 30px;
    }
    .single-team {
        margin-bottom: 15px;
    }
    .contact-us,
    .partner-block {
        margin-bottom: 30px;
    }
    .contact-us h3,
    .partner-block h3 {
        font-size: 28px;
    }
    .footer-links li a {
        margin-right: 10px;
    }
    .copyright {
        float: left;
    }
}